function shuffle(array) {
  let currentIndex = array.length,  randomIndex;

  // While there remain elements to shuffle.
  while (currentIndex != 0) {

    // Pick a remaining element.
    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex--;

    // And swap it with the current element.
    [array[currentIndex], array[randomIndex]] = [
      array[randomIndex], array[currentIndex]];
  }

  return array;
}

function MapColour(string) {
switch (string) {

  case "r":
  string = "pat-red";
  break;

  case "g":
  string = "pat-green";
  break;
  
  case "b":
  string = "pat-blue";
  break;

  case "y":
  string = "pat-yellow";
  break;

  case "m":
  string = "pat-black";
  break;

default:
  string = "";
}
return string;
}

function CubeColour(string) {
  switch (string) {
  
    case "r":
    string = "redCube";
    break;
  
    case "g":
    string = "greenCube";
    break;
    
    case "b":
    string = "blueCube";
    break;
  
    case "y":
    string = "yellowCube";
    break;
  
    case "m":
    string = "blackCube";
    break;
  
  default:
    string = "";
  }
  return string;
  }

function configParser(huntIndex,huntParameter,outputParameter){

if (huntParameter == "cityIndex")  {var RegExHunt = new RegExp("["+huntIndex+"][rgbym][1-5][rgbym]{5}")};
if (huntParameter == "cityColor")  {var RegExHunt = new RegExp("[0-9]"+huntIndex+"[1-5][rgbym]{5}")};

var CityConfig = RegExHunt.exec(seed_String);
if (CityConfig === null)
{return "standardTown"}

else {
switch (outputParameter) {

  case "color":
  var returnedValue = (CityConfig.toString()).charAt(1);
  break;
  
  case "value":
  var returnedValue = (CityConfig.toString()).charAt(2);
  break;


  case "cube0":
    var returnedValue = (CityConfig.toString()).charAt(3);
  break;

  case "cube1":
    var returnedValue = (CityConfig.toString()).charAt(4);
  break;

  case "cube2":
    var returnedValue = (CityConfig.toString()).charAt(5);
  break;
  
  case "cube3":
    var returnedValue = (CityConfig.toString()).charAt(6);
  break;
  
  case "cube4":
    var returnedValue = (CityConfig.toString()).charAt(7);
  break;

default:
  var returnedValue = CityConfig;
}

return returnedValue;
}
}

