var arr_locations = (arr_locations_pool.slice(0,5)).sort();
var arr_colors = ["r", "b", "g", "y", "m"];
shuffle(arr_colors);
var arr_values = [1, 2, 3, 4, 5];
shuffle(arr_values);

var arr_cube_pool = [];
for (var counter = 0; counter <19; counter++) 
{
  arr_cube_pool.push("r");
  arr_cube_pool.push("g");
  arr_cube_pool.push("b");
  arr_cube_pool.push("y");
  arr_cube_pool.push("m");
}
